import React from 'react';
import { StaticImage, GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from 'gatsby';
import { MDXRenderer } from 'gatsby-plugin-mdx';

import Layout from '../components/layout';
//import Img from 'gatsby-image';
import './blogPostTemplate.css';


export default ({ data }) => {
  const { frontmatter, body, fields } = data.mdx;
 // console.log(frontmatter.featuredImage);
  return (
    <Layout>
      <h1 className="text-secondary">{frontmatter.title}</h1>
      <p>{frontmatter.date}</p>
      <p>{'econolabs.github.io' + fields.slug}</p>
      {/* <img src={frontmatter.featuredImage}/> */}
   
     <RenderSmoothImage src={frontmatter.featuredImage} alt={frontmatter.title} /> 
     <BlogPost data={frontmatter.featuredImage}/>
      <MDXRenderer>{body}</MDXRenderer>
    </Layout>
  );
};

export const query = graphql`
  query PostsBySlug($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      frontmatter {
        title
        date(formatString: "YYYY MMMM Do")
        featuredImage 
      }
      fields {
        slug
      }
    }
  }
`;

function BlogPost({ data }) {
  const image = getImage(data)
  return (
    <section>
     
      <GatsbyImage image={image} alt={""} />
     
    </section>
  )
 }

function RenderSmoothImage({ src, alt }) {
  const [imageLoaded, setImageLoaded] = React.useState(false);

  return (
    <div>
      {imageLoaded ? <img
        src={src}
        alt={alt}
        style={{ visibility: 'visible' }}
      /> :
        <div className="fade-in" style={{ width: '640px', height: '400px', borderStyle: 'solid', borderColor: 'lightgray', borderWidth: '1px' }} >
          <img
            src={src}
            alt={alt}
            style={{ visibility: 'hidden' }}
            // className={imageLoaded ? 'visible' : 'hidden'}
            onLoad={() => setImageLoaded(true)}
          />
        </div>
      }
    </div>
  )
}